import { Injectable } from '@angular/core';
import { Avatar } from '../../components/avatar/avatar.component';
import { DataStore } from '../../utils/data-store';
import { LpxNavbarItem } from '../../components/navbar/models';

export type UserActionGroup = LpxNavbarItem[];

export interface UserProfile {
  userName: string;
  fullName: string;
  email: string;
  avatar?: Avatar;
  userActionGroups?: UserActionGroup[];
}

@Injectable({
  providedIn: 'root',
})
export class UserProfileService {
  private store = new DataStore({} as UserProfile);
  user$ = this.store.sliceState((state) => state);

  setUser(user: UserProfile) {
    this.store.set(user);
  }

  patchUser(user: Partial<UserProfile>) {
    this.store.patch(user);
  }
}
